body, html, #root, ul {
    margin: 0;
    padding: 0;
    height: 100%;
    overflow-x: hidden;
}

.nav-link{
    display: block;
    color: white;
    text-decoration: none;
    position: relative;
}

.nav-link::after{
    content: "";
    background: white;
    mix-blend-mode: exclusion;
    width: calc(100% + 20px);
    height: 0;
    position: absolute;
    bottom: -4px;
    left: -10px;
    transition: all .4s cubic-bezier(0.445, 0.05, 0.55, 0.95);
}

.nav-link:hover::after{
    height: calc(100% + 8px)
}

hr {
    position: relative;
    top: 20px;
    border: none;
    height: 12px;
    background: #153040;
    margin-bottom: 50px;
    clip-path: polygon(1% 0%, 100% 0, 99% 100%, 0% 100%);
}